.mndnt-progress-bar {
    margin: 20px auto;
    padding: 0;
    width: 90%;
    height: 1rem;
    overflow: hidden;
    background: #e5e5e5;
    border-radius: 6px;
}

.mndnt-progress-bar .bar {
    position: relative;
    float: left;
    min-width: 1%;
    height: 100%;
    background: cornflowerblue;
}

.mndnt-progress-bar .percent {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    margin: 0;
    font-family: tahoma, arial, helvetica;
    font-size: 12px;
    color: white;
}


.mndnt-progressBar-column .ui-table__cell__content {
    width: 90%;
}