body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}




.comments {
  background-color: #f5f5f5;
  padding: 10px;
  height: 100%;
}

.comments-title {
  font-size: 30px;
  margin-bottom: 20px;
}

.comments-container {
  /* margin-top: 40px; */

  /* padding: 0.5rem; */
  margin-bottom: 3px;
}

.comment-form-title {
  font-size: 22px;
}

.comment-form-textarea {
  width: 100%;
  height: 80px;
  margin-bottom: 8px;
  margin-top: 8px;
  border: 1px solid rgb(107, 114, 12);
}

.comment-form-button {
  font-size: 16px;
  padding: 8px 16px;
  background: rgb(59, 130, 246);
  border-radius: 8px;
  color: white;
}

.comment-form-button:hover:enabled {
  cursor: pointer;
  background: rgb(37, 99, 235);
}

.comment-form-button:disabled {
  opacity: 0.7;
  cursor: default;
}

.comment-form-cancel-button {
  margin-left: 10px;
}

.comment {
  display: flex;
  background-color: #fff;
  border: 1px solid #eee;

  padding: 5px;
}

.comment:not(:last-child) {
  margin-bottom: 5px;

}

.comment .ui-menu {
  border-width: 0px;
}

.comment .ui-menu li .ui-menu__item {
  border: 1px solid #e1dfdd;
  background-color: #f5f5f5;
}

.comment:last-child {
  margin-bottom: 0;
  border-top: 0px solid #eee;
}

.comment:hover {}

.comment:nth-child(even) {
  /* background-color: #f5f5f5; */

}

.comment-image-container {
  margin-right: 12px;
}

.comment-image-container img {
  border-radius: 50px;
}

.comment-right-part {
  width: 100%;
}

.comment-content {
  display: flex;
  margin-bottom: 0.5rem;
}

.comment-author {
  margin-right: 8px;
  font-size: 20px;
  color: rgb(59, 130, 246);
}

.comment-text {
  font-size: 18px;
}

.comment-actions {
  display: flex;
  font-size: 12px;
  color: rgb(51, 51, 51);
  cursor: pointer;
  margin-top: 8px;
}

.comment-action {
  margin-right: 8px;
}

.comment-action:hover {
  text-decoration: underline;
}

