.hide-replies {
  display: none;
}

.replies {
  /* margin-top: 20px; */
  display: block;
}




.dialog-container {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 600px;



  height: calc(50vh - 100px);
  /* Set dialog height */
}

/* Content styles */
.dialog-content {
  flex: 1;
  /* This makes the content section grow to fill available space */
  padding: 20px;
}

/* Footer styles */
.dialog-footer {
  padding: 20px;
  background-color: #f0f0f0;
  border-top: 1px solid #ccc;
}